import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Table, Checkbox, Card, Row, Col } from 'antd';
import { setTypeScoreForEnteringProcessScore } from 'dump/data';
import { BtnSave, BtnDelete, BtnCancel } from 'components/common/button';

class TableTypeScore extends Component {
  constructor(props) {
    super(props);
    this.state = {};

    this.columns = [
      {
        title: 'STT',
        dataIndex: 'stt',
        key: 'stt',
      },
      {
        title: 'Ký hiệu',
        dataIndex: 'kihieu',
        key: 'kihieu',
      },
      {
        title: 'Tên thành phần',
        dataIndex: 'tenthanhphan',
        key: 'tenthanhphan',
      },
      {
        title: 'Hệ số',
        dataIndex: 'heso',
        key: 'heso',
      },
      {
        title: 'Nhóm thành phần',
        dataIndex: 'nhomthanhphan',
        key: 'nhomthanhphan',
      },
      {
        title: 'Tỷ lệ nhóm',
        dataIndex: 'tylenhom',
        key: 'tylenhom',
      },
      {
        title: 'Chuyên cần',
        dataIndex: 'chuyencan',
        render: () =>
          setTypeScoreForEnteringProcessScore.length >= 1 ? (
            <Fragment>
              <Checkbox />
            </Fragment>
          ) : null,
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        render: () => (
          <BtnDelete type="error" className="btnSmallAddInTable" onClick={this.showModal}>
            Xóa
          </BtnDelete>
        ),
      },
    ];

    this.rowSelection = {
      onChange: (selectedRowKeys, selectedRows) => {
        console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
      },
      getCheckboxProps: record => ({
        disabled: record.name === 'Disabled User', // Column configuration not to be checked
        name: record.name,
      }),
    };
  }

  render() {
    return (
      <Card title="Form thay đổi thành phần">
        <Row className="rowMargin">
          <Col span={24}>
            <div style={{ float: 'right' }}>
              <BtnSave title="Lưu TP môn" />
              <BtnCancel onClick={() => this.props.history.goBack()} />
            </div>
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <Table
              bordered
              rowSelection={this.rowSelection}
              dataSource={setTypeScoreForEnteringProcessScore}
              columns={this.columns}
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <TableTypeScore {...props} />
  </Page>
);



